/* RESET STYLES */

@import-normalize;

* {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  margin: 0;
  padding: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
}

/*-------------- ALL PAGES --------------*/

h1 {
  text-align: center;
  margin-bottom: 20px;
}

a {
  color: inherit;
  text-decoration: none;
}

li {
  list-style-type: none;
}

/*-------------- HEADER --------------*/

.logo {
  position: absolute;
  top: 5px;
  left: 5px;
}

.navbar {
  background-color: #256d85;
  color: white;
  display: flex;
  justify-content: space-between;
}

.nav-main {
  display: flex;
  gap: 50px;
  padding: 20px 0;
  margin-left: 230px;
}

.nav-main-item {
  display: flex;
  flex-direction: column;
  font-size: 13px;
  text-transform: uppercase;
  max-width: 51px;
}

.nav-main-item:hover {
  font-weight: bold;
}

.nav-main-icon {
  max-width: 51px;
  max-height: 51px;
}

.nav-main-text {
  align-self: center;
}

.nav-sub {
  display: flex;
  gap: 20px;
}

.login {
  background-color: #4c6793;
  color: white;
  border: 3px solid white;
  width: 270px;
  height: 70px;
  margin-top: 15px;
  margin-right: 5px;
  font-size: 13px;
  display: flex;
  align-items: center;
}

.login input {
  border: 1px solid white;
  max-width: 90px;
}

.login-group {
  display: flex;
  gap: 5px;
}

.login-set {
  display: flex;
  flex-direction: column;
  margin-left: 5px;
  gap: 3px;
}

.login-button {
  align-self: flex-end;
  background-color: white;
  color: #256d85;
  max-height: 17.5px;
  margin-left: 5px;
  padding: 3px;
  display: flex;
  align-items: center;
  border: 1px solid #256d85;
}

.login-button:hover {
  background-color: #256d85;
  color: white;
  font-weight: bold;
  border: 1px solid white;
  cursor: pointer;
}

.signup {
  margin-top: 5px;
  margin-left: 5px;
}

.signup-link {
  text-decoration: underline;
}

.signup-link:hover {
  font-weight: bold;
}

.nav-sub {
  background-color: #4c6793;
}

.nav-sub-list {
  display: flex;
  gap: 40px;
  padding: 10px 0;
  margin-left: 230px;
  color: white;
}

.nav-sub-link {
  width: 42px;
  font-size: 14px;
}

.nav-sub-link:hover {
  font-weight: bold;
}

/*-------------- FOOTER --------------*/

footer {
  background-color: #e3e9f0;
  margin-top: 60px;
  padding: 10px 0;
  color: grey;
}

.footer-links {
  display: flex;
  justify-content: space-around;
}

.footer-link {
  width: 130px;
}

.footer-link:hover {
  color: black;
  font-weight: bold;
}

/*-------------- HOME --------------*/

.center-page {
  margin-left: 20%;
  margin-right: 20%;
  margin-top: 60px;
  display: flex;
  flex-direction: column;
  text-align: justify;
}

.home-intro-p {
  margin-bottom: 40px;
}

.read-more {
  color: #5c2e7e;
}

.read-more:hover {
  font-weight: bold;
}

.home-main-image {
  border: 3px solid #5c2e7e;
  max-width: 800px;
  align-self: center;
}

/*-------------- CONTACT --------------*/
.contact-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}
